'use client';

import { useState, useEffect, useRef } from 'react';
import { io, Socket } from 'socket.io-client';

// --- Types ---

type Phase = 'waiting' | 'intro' | 'roundA' | 'roundB' | 'ad' | 'voting' | 'rage' | 'finished';
type Player = 'A' | 'B';
type Role = 'viewer' | 'admin' | 'debater';

interface Tomato {
  id: number;
  x: number;
}

interface Heart {
  id: number;
  x: number;
}

interface Message {
  id: string; // Changed to string to match backend
  user: string;
  text: string;
  isDonation: boolean;
  amount?: number;
}

interface Donation {
  user: string;
  amount: number;
}

interface RoomState {
  phase: Phase;
  timeLeft: number;
  activePlayer: Player | null;
  viewersCount: number;
  chatMessages: Message[];
  donations: Donation[];
}

// --- Component ---

export default function RoomPage() {
  // --- Game State (from Server) ---
  const [phase, setPhase] = useState<Phase>('waiting');
  const [timeLeft, setTimeLeft] = useState(0);
  const [activePlayer, setActivePlayer] = useState<Player | null>(null);
  const [viewersCount, setViewersCount] = useState(0);
  const [messages, setMessages] = useState<Message[]>([]);
  const [donations, setDonations] = useState<Donation[]>([]);
  
  // --- Local UI State ---
  const [userRole, setUserRole] = useState<Role>('viewer');
  const [tomatoes, setTomatoes] = useState<Tomato[]>([]);
  const [hearts, setHearts] = useState<Heart[]>([]);
  const [chatInput, setChatInput] = useState("");

  const chatEndRef = useRef<HTMLDivElement>(null);
  const socketRef = useRef<Socket | null>(null);

  // --- WebSocket Connection ---
  useEffect(() => {
    // Connect to backend
    const socket = io('http://localhost:4000');
    socketRef.current = socket;

    socket.on('connect', () => {
      console.log('Connected to server');
    });

    socket.on('state_update', (state: RoomState) => {
      setPhase(state.phase);
      setTimeLeft(state.timeLeft);
      setActivePlayer(state.activePlayer);
      setViewersCount(state.viewersCount);
      // We can also sync messages/donations here if needed, 
      // but chat_update is usually separate for efficiency.
      // However, initial load might need it.
      // For simplicity let's rely on chat_update event or state dump.
      // If server sends full history in state_update:
      if (state.chatMessages) setMessages(state.chatMessages);
      if (state.donations) setDonations(state.donations);
    });

    socket.on('chat_update', (newMessages: Message[]) => {
      setMessages(newMessages);
    });

    socket.on('reaction_received', (payload: { type: 'tomato' | 'heart' }) => {
      if (payload.type === 'tomato') {
        spawnTomato();
      } else if (payload.type === 'heart') {
        spawnHeart();
      }
    });

    return () => {
      socket.disconnect();
    };
  }, []);

  // Scroll chat to bottom
  useEffect(() => {
    chatEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  // --- Handlers (Sending to Server) ---

  const handleStart = () => {
    socketRef.current?.emit('admin_action', { action: 'start' });
  };

  const handleNextPhase = () => {
    socketRef.current?.emit('admin_action', { action: 'next_round' });
  };

  const handleReset = () => {
    socketRef.current?.emit('admin_action', { action: 'reset' });
  };

  const handleForceRage = () => {
    socketRef.current?.emit('admin_action', { action: 'rage_mode' });
  };

  const handleSendTomato = () => {
    // Optimistic UI update + server emit
    spawnTomato(); 
    socketRef.current?.emit('send_reaction', { type: 'tomato' });
  };

  const handleSendHeart = () => {
    spawnHeart();
    socketRef.current?.emit('send_reaction', { type: 'heart' });
  };

  const handleDonate = () => {
    // Mock donation from "Me"
    socketRef.current?.emit('send_message', { 
      user: 'Me', 
      text: '$$$ 500‚ÇΩ Donation!', 
      isDonation: true, 
      amount: 500 
    });
    // Also send heart reaction
    handleSendHeart();
  };

  const handleChatSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!chatInput.trim()) return;
    
    socketRef.current?.emit('send_message', {
      user: 'Me',
      text: chatInput,
      isDonation: false
    });
    setChatInput("");
  };

  // --- Local Animation Helpers ---

  const spawnTomato = () => {
    const id = Date.now() + Math.random();
    const x = 5 + Math.random() * 20; // Left side
    setTomatoes((prev) => [...prev, { id, x }]);
  };

  const spawnHeart = () => {
    const id = Date.now() + Math.random();
    const x = 75 + Math.random() * 20; // Right side
    setHearts((prev) => [...prev, { id, x }]);
  };

  // --- Render Helpers ---

  const isPlayerAActive = (phase === 'roundA' || phase === 'rage') && (activePlayer === 'A' || phase === 'rage');
  const isPlayerBActive = (phase === 'roundB' || phase === 'rage') && (activePlayer === 'B' || phase === 'rage');
  const isLowTime = timeLeft <= 10;
  
  // Format time mm:ss
  const minutes = Math.floor(timeLeft / 60);
  const seconds = timeLeft % 60;
  const formattedTime = `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;

  // Helper for phase display text
  const getPhaseLabel = () => {
    switch (phase) {
      case 'intro': return 'INTRO';
      case 'roundA': 
      case 'roundB': return 'ROUND';
      case 'rage': return 'RAGE MODE';
      case 'voting': return 'VOTING';
      default: return '';
    }
  };

  return (
    <main className="relative flex h-screen flex-col bg-slate-950 font-sans text-white overflow-hidden selection:bg-blue-500/30">
      
      {/* --- Global Styles for Animations --- */}
      <style jsx global>{`
        @keyframes floatUp {
          0% { bottom: 10%; opacity: 1; transform: translateX(0) scale(0.5); }
          25% { transform: translateX(5px) scale(0.8); }
          50% { transform: translateX(-5px) scale(1); }
          75% { transform: translateX(5px) scale(1.1); }
          100% { bottom: 60%; opacity: 0; transform: translateX(0) scale(1.2); }
        }
        .reaction-float {
          animation: floatUp 1.2s ease-out forwards;
        }
        .rage-pulse {
          animation: pulse 0.5s cubic-bezier(0.4, 0, 0.6, 1) infinite;
        }
      `}</style>

      {/* --- Dev Toolbar (Top Right) --- */}
      <div className="fixed right-4 top-4 z-50 flex gap-2 rounded-lg bg-black/60 p-2 text-xs backdrop-blur-md border border-white/10 shadow-xl">
        <span className="text-slate-400 self-center mr-2">ROLE:</span>
        {(['viewer', 'admin', 'debater'] as Role[]).map((role) => (
          <button
            key={role}
            onClick={() => setUserRole(role)}
            className={`px-3 py-1 rounded transition-colors ${
              userRole === role 
                ? 'bg-blue-600 text-white font-bold shadow-lg shadow-blue-500/20' 
                : 'bg-white/10 hover:bg-white/20 text-slate-300'
            }`}
          >
            {role.toUpperCase()}
          </button>
        ))}
      </div>

      {/* --- Reaction Overlay Layer --- */}
      <div className="pointer-events-none fixed inset-0 z-40 overflow-hidden">
        {tomatoes.map((t) => (
          <div key={t.id} className="reaction-float absolute text-4xl drop-shadow-lg" 
               style={{ left: `${t.x}%` }} onAnimationEnd={() => setTomatoes(p => p.filter(i => i.id !== t.id))}>
            üçÖ
          </div>
        ))}
        {hearts.map((h) => (
          <div key={h.id} className="reaction-float absolute text-4xl drop-shadow-lg" 
               style={{ left: `${h.x}%` }} onAnimationEnd={() => setHearts(p => p.filter(i => i.id !== h.id))}>
            ‚ù§Ô∏è
          </div>
        ))}
      </div>

      {/* --- Main Content (Videos + Center Overlay) --- */}
      <div className="flex flex-1 flex-col overflow-hidden relative">
        <div className="flex flex-1 items-center justify-center p-6 pb-2">
          <div className="relative flex h-full w-full max-w-[1600px] items-center justify-center gap-6">
            
            {/* Player A (Left) */}
            <div className={`relative flex h-full w-1/2 flex-col items-center justify-center overflow-hidden rounded-3xl border-4 transition-all duration-500 ${
              isPlayerAActive
                ? 'border-red-500 shadow-[0_0_60px_rgba(239,68,68,0.3)] bg-slate-900'
                : 'border-slate-800 bg-slate-900/40 opacity-50 grayscale-[0.5]'
            }`}>
              <span className="text-2xl font-bold text-slate-600 animate-pulse">Waiting for Video...</span>
              <div className="absolute left-6 top-6 flex items-center gap-3">
                 <div className="h-3 w-3 rounded-full bg-red-500 shadow-[0_0_10px_red]"></div>
                 <span className="text-sm font-bold uppercase tracking-widest text-red-200">Player A</span>
              </div>
            </div>

            {/* Center HUD (Timer / Status) */}
            <div className="absolute left-1/2 top-1/2 z-30 -translate-x-1/2 -translate-y-1/2 transform text-center">
               {phase === 'waiting' && (
                  <div className="px-8 py-4 bg-black/50 backdrop-blur-xl rounded-2xl border border-white/10">
                    <h1 className="text-4xl font-black uppercase tracking-widest">Ready?</h1>
                    <p className="text-slate-400 mt-2 text-sm">Waiting for admin start...</p>
                  </div>
               )}

               {phase === 'ad' && (
                 <div className="flex h-40 w-[500px] items-center justify-center rounded-2xl bg-gradient-to-r from-yellow-400 to-orange-500 px-12 shadow-[0_0_50px_orange]">
                   <h2 className="text-5xl font-black uppercase tracking-widest text-white drop-shadow-md">–†–ï–ö–õ–ê–ú–ê</h2>
                 </div>
               )}

               {(phase === 'intro' || phase === 'roundA' || phase === 'roundB' || phase === 'rage') && (
                 <div className={`flex flex-col items-center justify-center rounded-2xl border bg-slate-950/80 px-8 py-4 shadow-2xl backdrop-blur-xl transition-colors duration-300 ${
                   phase === 'rage' ? 'border-red-600 shadow-red-900/50 rage-pulse' : 'border-slate-700 shadow-black/50'
                 } ${isLowTime ? 'border-red-500/50' : ''}`}>
                   
                   <div className={`font-mono text-5xl font-bold tracking-tight ${
                     phase === 'rage' ? 'text-red-500' : isLowTime ? 'text-red-400' : 'text-white'
                   }`}>
                     {formattedTime}
                   </div>
                   
                   <span className={`mt-1 text-[10px] font-bold uppercase tracking-[0.3em] ${
                     phase === 'rage' ? 'text-red-500' : 'text-slate-400'
                   }`}>
                     {getPhaseLabel()}
                   </span>
                 </div>
               )}

               {phase === 'finished' && (
                 <div className="rounded-2xl bg-black/80 px-10 py-6 backdrop-blur-md border border-white/10">
                   <h2 className="text-3xl font-bold text-white mb-2">Battle Finished</h2>
                   <button onClick={handleReset} className="text-sm text-blue-400 hover:text-blue-300 underline">Reset Game</button>
                 </div>
               )}
            </div>

            {/* Player B (Right) */}
            <div className={`relative flex h-full w-1/2 flex-col items-center justify-center overflow-hidden rounded-3xl border-4 transition-all duration-500 ${
              isPlayerBActive
                ? 'border-blue-500 shadow-[0_0_60px_rgba(59,130,246,0.3)] bg-slate-900'
                : 'border-slate-800 bg-slate-900/40 opacity-50 grayscale-[0.5]'
            }`}>
              <span className="text-2xl font-bold text-slate-600 animate-pulse">Waiting for Video...</span>
              <div className="absolute right-6 top-6 flex items-center gap-3 flex-row-reverse">
                 <div className="h-3 w-3 rounded-full bg-blue-500 shadow-[0_0_10px_blue]"></div>
                 <span className="text-sm font-bold uppercase tracking-widest text-blue-200">Player B</span>
              </div>
            </div>

          </div>
        </div>

        {/* Viewer Count & Info */}
        <div className="flex justify-center pb-2 z-20">
          <div className="flex items-center gap-3 rounded-full bg-slate-900/80 border border-slate-700/50 px-5 py-2 text-sm font-medium text-slate-300 backdrop-blur-sm shadow-lg">
            <span className="text-red-500 animate-pulse">‚óè</span>
            <span>LIVE</span>
            <span className="w-px h-4 bg-slate-700 mx-1"></span>
            <span>üëÅ {viewersCount.toLocaleString()} watching</span>
          </div>
        </div>

        {/* --- Viewer Reaction Bar (Overlay on Video) --- */}
        {userRole === 'viewer' && phase !== 'waiting' && phase !== 'finished' && (
          <div className="absolute bottom-6 left-1/2 -translate-x-1/2 z-40 flex gap-3 p-2 rounded-2xl bg-black/40 backdrop-blur-md border border-white/10 transition-all hover:bg-black/60">
            <button 
              onClick={handleSendTomato}
              className="group flex flex-col items-center gap-1 rounded-xl bg-slate-800/50 px-4 py-2 hover:bg-red-500/20 hover:scale-105 transition-all active:scale-95"
            >
              <span className="text-2xl group-hover:rotate-12 transition-transform">üçÖ</span>
              <span className="text-[10px] font-bold text-slate-300">THROW</span>
            </button>
            <button 
              onClick={handleSendHeart}
              className="group flex flex-col items-center gap-1 rounded-xl bg-slate-800/50 px-4 py-2 hover:bg-pink-500/20 hover:scale-105 transition-all active:scale-95"
            >
              <span className="text-2xl group-hover:-translate-y-1 transition-transform">‚ù§Ô∏è</span>
              <span className="text-[10px] font-bold text-slate-300">LOVE</span>
            </button>
            <button 
              onClick={handleDonate}
              className="group flex flex-col items-center gap-1 rounded-xl bg-gradient-to-b from-yellow-600/20 to-yellow-800/20 px-4 py-2 hover:from-yellow-500/30 hover:to-yellow-700/30 border border-yellow-500/30 hover:scale-105 transition-all active:scale-95"
            >
              <span className="text-2xl">üí∞</span>
              <span className="text-[10px] font-bold text-yellow-200">DONATE</span>
            </button>
          </div>
        )}

        {/* --- Debater UI Indicator --- */}
        {userRole === 'debater' && (
          <div className="absolute bottom-6 left-1/2 -translate-x-1/2 z-40 px-6 py-3 rounded-full bg-slate-900 border border-slate-700 flex items-center gap-3 shadow-2xl">
            <div className={`h-4 w-4 rounded-full ${isPlayerAActive || isPlayerBActive ? 'bg-green-500 animate-pulse' : 'bg-red-500'}`}></div>
            <span className="font-bold text-slate-200 tracking-wide">MICROPHONE ACTIVE</span>
          </div>
        )}

      </div>

      {/* --- Admin Control Deck --- */}
      {userRole === 'admin' && (
        <div className="border-t border-slate-800 bg-slate-900 p-3 flex justify-center gap-4 z-50 shadow-[0_-10px_40px_rgba(0,0,0,0.5)]">
          <button onClick={handleStart} className="px-6 py-2 bg-emerald-600 hover:bg-emerald-500 text-white rounded font-bold uppercase text-xs tracking-wider shadow-lg">
            Start Battle
          </button>
          <button onClick={handleNextPhase} className="px-6 py-2 bg-blue-600 hover:bg-blue-500 text-white rounded font-bold uppercase text-xs tracking-wider shadow-lg">
            Next Phase
          </button>
          <button onClick={handleForceRage} className="px-6 py-2 bg-red-600 hover:bg-red-500 text-white rounded font-bold uppercase text-xs tracking-wider shadow-lg animate-pulse">
            Force Rage
          </button>
          <button onClick={handleReset} className="px-6 py-2 bg-slate-700 hover:bg-slate-600 text-slate-300 rounded font-bold uppercase text-xs tracking-wider shadow-lg">
            Reset
          </button>
        </div>
      )}

      {/* --- Bottom Panel (3 Columns) --- */}
      <div className="grid h-[200px] w-full flex-shrink-0 grid-cols-3 divide-x divide-slate-800 border-t border-slate-800 bg-slate-950 text-slate-300 z-10">
        
        {/* Col 1: Participants */}
        <div className="flex h-full flex-col overflow-hidden bg-slate-900/50">
          <div className="flex items-center justify-between border-b border-slate-800 bg-slate-900 px-4 py-2">
             <span className="text-xs font-bold uppercase tracking-wider text-slate-500">Participants</span>
             <span className="text-xs font-mono text-slate-600">{viewersCount} ONLINE</span>
          </div>
          <div className="flex-1 p-4 text-sm text-slate-500 flex items-center justify-center italic">
            List hidden for privacy
          </div>
        </div>

        {/* Col 2: Chat */}
        <div className="flex h-full flex-col overflow-hidden bg-slate-900 relative">
          <div className="flex items-center justify-between border-b border-slate-800 bg-slate-900 px-4 py-2">
            <span className="text-xs font-bold uppercase tracking-wider text-slate-500">Live Chat</span>
            {userRole === 'admin' && (
              <button className="text-[10px] text-red-400 hover:text-red-300 uppercase font-bold">Clear (Server)</button>
            )}
          </div>
          <div className="flex-1 overflow-y-auto p-4 space-y-2 scrollbar-thin scrollbar-thumb-slate-700 scrollbar-track-transparent">
            {messages.length === 0 && (
              <div className="text-center text-xs text-slate-600 mt-10">Chat is empty...</div>
            )}
            {messages.map((msg) => (
              <div 
                key={msg.id} 
                className={`flex flex-wrap items-baseline gap-2 rounded px-2 py-1 text-sm transition-all animate-in fade-in slide-in-from-bottom-2 duration-300 ${
                  msg.isDonation ? 'bg-yellow-500/10 border border-yellow-500/20 shadow-sm' : 'hover:bg-white/5'
                }`}
              >
                <span className={`font-bold text-xs ${
                  msg.isDonation ? 'text-yellow-400' : 'text-blue-400'
                }`}>
                  {msg.user}:
                </span>
                <span className={`text-slate-300 ${msg.isDonation ? 'font-medium text-yellow-100' : ''}`}>
                  {msg.isDonation && 'üí∞ '}
                  {msg.text}
                </span>
              </div>
            ))}
            <div ref={chatEndRef} />
          </div>
          {/* Chat Input */}
          <form onSubmit={handleChatSubmit} className="p-2 border-t border-slate-800 bg-slate-900 flex gap-2">
             <input
               value={chatInput}
               onChange={(e) => setChatInput(e.target.value)}
               className="h-8 w-full rounded bg-slate-800 text-xs text-slate-300 px-3 outline-none focus:ring-1 focus:ring-blue-500"
               placeholder={userRole === 'viewer' ? "Type a message..." : "Chat disabled"}
               disabled={userRole !== 'viewer' && userRole !== 'admin'}
             />
             <button type="submit" className="px-3 bg-blue-600 rounded text-xs font-bold hover:bg-blue-500">SEND</button>
          </form>
        </div>

        {/* Col 3: Donations */}
        <div className="flex h-full flex-col overflow-hidden bg-slate-900/50">
          <div className="border-b border-slate-800 bg-slate-900 px-4 py-2 text-xs font-bold uppercase tracking-wider text-slate-500">
            Top Donators
          </div>
          <div className="flex-1 overflow-y-auto p-2 space-y-1">
             {donations.map((d, i) => (
               <div key={i} className="flex items-center justify-between bg-slate-800/30 p-2 rounded border border-white/5">
                  <div className="flex items-center gap-2">
                    <div className="h-6 w-6 rounded-full bg-gradient-to-tr from-yellow-600 to-yellow-400 flex items-center justify-center text-[10px] font-bold text-black border border-yellow-300">
                      {i + 1}
                    </div>
                    <span className="text-xs font-bold text-slate-300">{d.user}</span>
                  </div>
                  <span className="text-xs font-mono text-yellow-500">{d.amount}‚ÇΩ</span>
               </div>
             ))}
             {donations.length === 0 && <div className="text-center text-xs text-slate-600 mt-10">No donations yet</div>}
          </div>
        </div>

      </div>

    </main>
  );
}
